@font-face {
  font-family: "Nekst-Black";
  src: url("./assets/fonts/Nekst-Black.woff2");
}

@font-face {
  font-family: "Gilroy";
  src: url("./assets/fonts/Gilroy-Regular.woff2");
}

.main {
  margin: 0;
  padding: 0;
}

.header {
  width: 753px;
  height: 98px;
  margin-top: 100px;
  margin-left: 48px;
  margin-bottom: 44px;
  padding: 0;
  font-family: "Nekst-Black", sans-serif;
  font-size: 54px;
  line-height: 90%;
}

form {
  width: 928px;
  margin-left: 48px;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

input[type="number"] {
	-moz-appearance: textfield;
	-webkit-appearance: textfield;
	appearance: textfield;
}
 
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
	display: none;
}

.inner-sign {
  width: auto;
  height: 36px;
  right: 16px;
  top: 16px;
  background-color: #F3F3F4;
  text-align: center;
  color: #575757;
  font-size: 30px;
  line-height: 120%;
  font-family: "Nekst-Black", sans-serif;
}

.input-el {
  margin-bottom: 32px;
}

.input-title {
  font-family: "Gilroy", sans-serif;
  font-size: 16px;
  line-height: 125%;
}

.input-field {
  display: block;
  box-sizing: border-box;
  margin-top: 24px;
  margin-bottom: 7px;
  padding: 16px 24px;
  width: 100%;
  height: 68px;
  background-color: #F3F3F4;
  border-radius: 16px;
  color: #575757;
  font-family: "Nekst-Black", sans-serif;
  font-size: 30px;
  line-height: 120%;
  border: none;
}

.input-field-small {
  box-sizing: border-box;
  right: 7px;
  top: 7px;
  width: 70px;
  height: 54px;
  padding-left: 18px;
  background-color: #E0E0E0;
  border-radius: 16px;
  color: #575757;
  font-family: "Nekst-Black", sans-serif;
  font-size: 20px;
  line-height: 120%;
  border: none;
}

.inner-sign-small {
  top: 22px;
  right: 22px;
  width: auto;
  height: auto;
  background-color: #E0E0E0;
  color: #575757;
  text-align: center;
  font-family: "Nekst-Black", sans-serif;
  font-size: 20px;
  line-height: 120%;
}

.input-field:focus,
.input-field-small:focus {
  background-color: white;
  outline: none;
  border: 2px solid #F3F3F4;
}

.input-field:focus + .inner-sign {
  background-color: white;
}

.input-field-small:focus + .inner-sign-small {
  background-color: white;
}

input[type="range"] {
  top: 64px;
  left: 24px;
  width: 812px;
	-webkit-appearance: none;
}

input[type="range"].w-short {
  width: 631px;
}

input[type="range"]::-webkit-slider-runnable-track {
  display: block;
  box-sizing: border-box;
  height: 2px;
  background: #FF9514;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: #FF9514;
  width: 20px;
  height: 20px;
  margin-top: -9px;
  border-radius: 50%;
}

input[type="range"]::-moz-range-track {
  height: 2px;
  background: #FF9514;
}

input[type="range"]::-moz-range-thumb {
  background: #FF9514;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: none;
}
